body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  font-family: HelveticaNeue, Arial, sans-serif; }

.site-container,
.site-container:after {
  min-height: 100%;
  display: flex;
  flex: 1;
  flex-direction: column; }

.site-container {
  background-image: url("/img/image_bg.jpg");
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  position: relative; }

.site-container:after {
  content: '';
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1; }

.logo,
.site-footer {
  position: relative;
  z-index: 10;
  padding: 0 1rem; }

.symbol,
.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 10; }

.symbol {
  margin-top: 3rem; }
  .symbol img {
    width: 100%;
    max-height: 15vh; }

.logo {
  margin-top: 4rem; }
  .logo img {
    width: 100%;
    max-height: 20vh; }

.soundcloud__embed {
  width: 100%;
  margin: 4rem auto 4rem;
  position: relative;
  z-index: 10;
  overflow: hidden;
  height: 20vh; }
  @media (min-width: 600px) {
