.soundcloud__controls {
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 20;
  transition: all .7s ease-in-out;
  transition-delay: .3s;
  width: 0;
  overflow: hidden; }

.soundcloud__control {
  font-size: 3rem;
  margin: 0 1rem;
  color: #bcbcbc;
  cursor: pointer; }
  .soundcloud__control:hover {
    color: #fff; }

